import React, { useContext } from 'react';
import { View, StyleSheet, Text } from 'react-native';
import { SIZES } from '../constants';
import { CityContext } from '../context';

import { gStyle } from '../styles/style';
import { CloudyIcon, SnowIcon, RainIcon, StormIcon, SunnyIcon } from './icons/icons';


const Body = () => {
  const {currentCity, setcurrentCity} = useContext(CityContext);

  const weatherIcon = weatherType => {
    switch (weatherType) {
      case 'Snow':
        return <SnowIcon/>;
      
      case 'Rain':
        return <RainIcon/>;

      case 'Drizzle':
        return <RainIcon/>;

      case 'Thunderstorm':
        return <StormIcon/>;

      case 'Clear':
        return <SunnyIcon/>;

      default:
        return <CloudyIcon/>;
    }
  };
  return (
    <View style={styles.body}>
      <View style={[gStyle.directionRow, styles.top]}>
        <View style={styles.icon}>
          {weatherIcon(currentCity.weather)}
        </View>
        <Text style={[gStyle.title, styles.titleBody]}>{currentCity.temp}&#176;</Text>
      </View>
      <Text style={[gStyle.text, styles.textBody]}>{currentCity.description}</Text>
    </View>
  );
};
  
export default Body;

const styles = StyleSheet.create({ 
  body: {
    flex: 2,
    justifyContent: 'center',
    alignItems: 'center'
  },
  top: {
    alignItems: 'center'
  },
  icon: {
    height: SIZES.iconSize,
    width: SIZES.iconSize,
  },
  titleBody: {
    marginLeft: 25,
    fontSize: SIZES.largeTitle,
    lineHeight: 190,
  },
  textBody: {
    fontFamily: 'Lato-Regular',
    textTransform: 'capitalize',
    fontSize: SIZES.subtitle
  },
});