import React, { useContext } from 'react';
import { View, StyleSheet, Text} from 'react-native';
import { SIZES } from '../constants';
import { CityContext } from '../context';

import { gStyle } from '../styles/style';

const Bottom = () => {
  const {currentCity, setcurrentCity} = useContext(CityContext);

  return (
    <View style={styles.bottom}>
      <View style={styles.itemBottom}>
        <Text style={[gStyle.title, styles.titleBottom]}>
          Ветер
        </Text>
        <Text style={[gStyle.text, styles.textBottom]}>
          {currentCity.windSpeed} м/с, {currentCity.windDirection}
        </Text>
      </View>
      <View style={styles.itemBottom}>
        <Text style={[gStyle.title, styles.titleBottom]}>
          Давление
        </Text>
        <Text style={[gStyle.text, styles.textBottom]}>
          {currentCity.pressure} мм рт. ст.
        </Text>
      </View>
      <View style={styles.itemBottom}>
        <Text style={[gStyle.title, styles.titleBottom]}>
          Влажность
        </Text>
        <Text style={[gStyle.text, styles.textBottom]}>
          {currentCity.humidity}%
        </Text>
      </View>
      <View style={styles.itemBottom}>
        <Text style={[gStyle.title, styles.titleBottom]}>
          Вероятность дождя
        </Text>
        <Text style={[gStyle.text, styles.textBottom]}>
          {currentCity.rain}%
        </Text>
      </View>
    </View>
  ) 
};

export default Bottom;

const styles = StyleSheet.create({ 
  bottom: {
    flex: 1,
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
  },
  itemBottom: {
    maxWidth: '270px',
    minWidth: '200px'
  },
  titleBottom: {
    fontSize: SIZES.opacityText,
    lineHeight: 21.6,
    opacity: 0.6
  },
  textBottom: {
    fontFamily: 'Lato-Bold',
    paddingBottom: 35
  }
})