import React, { useState } from 'react';
import { COORDINATES } from './constants/objects';
import { CityContext } from './context';


const GlobalStates = ({children}) => {
  const [openModal, setOpenModal] = useState(false);
  const [temp, setTemp] = useState('C');
  const [currentCity, setcurrentCity] = useState({});
  const [currentNameCity, setCurrentNameCity] = useState(Object.keys(COORDINATES)[0]);
  const [isLoadingWeather, setIsLoadingWeather] = useState(true);

  return (
    <CityContext.Provider value={{
      openModal, setOpenModal,
      currentNameCity,  setCurrentNameCity,
      temp,  setTemp,
      currentCity,  setcurrentCity,
      isLoadingWeather, setIsLoadingWeather,
    }}>
      {children}
    </CityContext.Provider>
  )
}

export default GlobalStates;